<template>
	<div>
		<el-card class="sign_wrap">
			<p style="color: #409EFF;font-weight: bolder;">注册</p>
			<div class="form">
				账号 <input v-model="email" ></input>
			</div>
			<div class="form">
				密码 <input v-model="password" type="password"></input>
			</div>

			<el-button type="primary" plain @click="sign" style="margin-bottom: 10px;">注册</el-button><br />
			<el-button type="primary" plain @click='login'>返回登录</el-button>
		</el-card>
	</div>
</template>

<script>
	export default{
		data:function(){
			return {
				email:'',
				password:''
			}
		},
		created:function(){
			document.title='用户注册'
		},
		methods:{
			sign:function(){this.$message.error('注册失败');},
			login:function(){
				this.$router.push({
					path: '/login',
				})
			}
		}
	}
</script>

<style lang="scss">
	.sign_wrap{
		height: 360px;
		width: 300px;
		margin: 80px auto;
		padding: 60px 25px 25px 25px;
		p{
			text-align: center;
		}
		.form{
			font-weight: bold;
			margin-bottom: 20px;
		}
		input{
			border-radius:5px;
			border: 1px solid #dcdfe6;
			height: 30px;
			width: 100%;
			font-size: 18px;
		}
		.el-button{
			font-weight: bolder;
			width: 100%;
		}
	}
</style>


