<template>
	<div>
		<p style="text-align: center;">账号：user</p>
		<p style="text-align: center;">密码：123</p>
		
		<el-card class="login_wrap">
			<p style="color: #409EFF;font-weight: bolder;">登录</p>
			<div class="form">
				Email
				<el-input v-model="email" placeholder="请输入内容"></el-input>
			</div>
			<div class="form">
				Password
				<el-input v-model="password" placeholder="请输入内容" show-password></el-input>
			</div>
			<el-button type="primary" @click="login">登录</el-button><br />
			<el-button type="primary" plain @click='sign'>注册</el-button>
		</el-card>
	</div>
</template>

<script>
	export default{
		created:function(){
			document.title='用户登录'
		},
		data:function(){
			return{
				email:'',
				password:''
			}
		},
		methods:{
			login:function(){
				if(this.email=="user"&&this.password==123){
					var item={email:"user",password:123};
					var it=JSON.stringify(item)
					document.cookie = "user=" + it + ";path=/";
					this.$router.push({
						path: '/home',
					})
					this.$message({
						message: '登陆成功',
						type: 'success'
					});
				}else{this.$message.error('账号错误');}
			},
			sign:function(){
				this.$router.push({
					path: '/sign',
				})
			}
		}
	}
</script>

<style lang="scss">
	body{
		font-family: "微软雅黑";
	}
	.el-card__body{
		padding: 0;
	}
	.login_wrap{
		height: 360px;
		width: 300px;
		margin: 80px auto;
		padding: 60px 25px 25px 25px;
		p {
			text-align: center;
		}
		.form{
			color: #409EFF;
			font-weight: bolder;
			margin-bottom: 30px;
		}
		input{
			height: 50px;
			background:#ecf8ff;
			font-size: 16px;
		}
		.el-button{
			font-weight: bolder;
			width: 100%;
		}
	}

</style>


