<template>
	<div>
		<router-view/>
	</div>
</template>

<script>
	import Vue from 'vue'
	import VueRouter from 'vue-router'
	import login from './login'
	import sign from './sign'
	import home from './home'
	Vue.use(VueRouter)
	var router = new VueRouter({
		routes: [
		{
			path: '/login',
			name: 'login',
			component: login
		}
		,{
			path: '/sign',
			name: 'sign',
			component: sign
		}
		,{
			path: '/home',
			name: 'home',
			component: home
		}
		]
	})
	
	export default{
		router: router,
	}
</script>

<style>
</style>


