		</el-card>
		<div class="body-contain">
		<div class="main" style="height: 1000px;">
			<div class="showlists">
				<div class="title">
					正在热映...
				</div>
				<div class="items">
					<div class="block">
						<el-carousel trigger="click" height="250px">
							<el-carousel-item v-for="(item,index) in 4" :key="item">

								<div class="item">
									<a class="dd" v-for="it in 5" style="display: block;" :href="href[index]" @click="href_href(index)">
										<div class="photo" style="">
											<img :src="img[index]"style="height: 100%;width: 100%;"/>
