										</div>
									</a>
								</div>
							</el-carousel-item>
						</el-carousel>
					</div>
				</div>
			</div>
			<div class="comments">
				<div class="title">
					最新影评...
				</div>
				<div class="items">
					<div class="lists" v-for="(com_item,index) in comments">
						<div class="list_title" style="height: 40px;line-height: 40px;" >
							<div class="left" style="height: 40px;line-height: 40px;float: left;">
								<img src="../../../static/movie/main/1.jpg" style="margin-right:10px;height: 20px;width: 20px;border-radius:10px;vertical-align: middle;"/>
								<a :href="home" style="margin-right: 10px;" @click="home_home">admin</a>
								<span>写了影评</span>
							</div>
							<div class="right" style="height: 40px;line-height: 40px;float: right;">
								2019-06-15 00:03
							</div>
						</div>
						<div class="lists_commo" style="height: 150px;" >
							<div class="left" style="height: 100%;float: left;line-height: 100%;">
								<a :href="href[index]" style="display: block;height: 100%;" @click="href_href(index)"><img :src="com_item" style="height: 100%;vertical-align: top;"/></a>
								呵呵
							</div>
							<div class="block" style="float: right;">
									<el-rate
										v-model="value[index]"
										disabled
										show-score
										:colors="colors">
									</el-rate>
							</div>
						</div>
					</div>
				</div>
			</div>
		</div>
		<div class="aside">
			<el-card class="one contents">
				<div class="title">
					公告
				</div>
				<el-divider></el-divider>
				<div class="text">
					无
				</div>
			</el-card>
			<el-card class="two contents">
				<div class="title">
					内地实时票房<span>单位：万(美元)</span>
				</div>
				<el-divider></el-divider>
				<div class="lists">
					<li v-for="(item,index) in aside.fir">
						<span class="index">{{index+1}}</span>
						<a>{{item.name}}</a>
						<span class="num">{{item.num}}</span>
					</li>
				</div>
			</el-card>
			
			<el-card class="thr contents">
				<div class="title">
					一周北美票房<span>单位：万(美元)</span>
				</div>
				<el-divider></el-divider>
				<div class="lists">
					<li v-for="(item,index) in aside.sec">
						<span class="index">{{index+1}}</span>
						<a>{{item.name}}</a>
						<span class="num">{{item.num}}</span>
					</li>
				</div>
			</el-card>
		</div>
		</div>
	</div>
</template>

<script>
	export default{
		data:function(){
			return {
				home:'aaaaa',
				href:['a','b','c','d'],
				img:['../../../static/movie/main/1.jpg','B','C','D'],
				name:['aa','bb','cc','dd'],
				 value:[3.5,4],
				 colors: ['#99A9BF', '#F7BA2A', '#FF9900'] ,
				 comments:['../../../static/movie/main/1.jpg','B'],
				info_show:false,
				aside:{
					fir:[
					{lin:1,name:'千与千寻',num:'2031.05 (7656.72)'},
					{lin:2,name:'玩具总动员4',num:'1518.55 (3352.51)'},
					{lin:3,name:'黑衣人：全球追缉',num:'436.11 (25691.67)'},
					{lin:4,name:'最好的我们',num:'219.56 (35986.18)'},
					{lin:5,name:'哥斯拉2：怪兽之王',num:'211.52 (8882.05)'},
					{lin:6,name:'绝杀慕尼黑',num:'206.06 (4831.20)'},
					{lin:7,name:'我的青春都是你',num:'110.85 (581.72)'},
