					{lin:10,name:'废柴老爸',num:'62.95 (470.57)'},
					],
					sec:[
					{lin:3,name:'黑衣人：全球追缉',num:'2850.00'},
					{lin:11,name:'爱宠大机密2',num:'2380.00'},
					{lin:9,name:'阿拉丁',num:'1671.20'},
					{lin:8,name:'X战警',num:'900.50'},
					{lin:12,name:'火箭人',num:'880.00'},
					{lin:13,name:'夏福特',num:'831.50'},
					{lin:5,name:'哥斯拉2：怪兽之王',num:'810.50'},
					{lin:14,name:'疾速备战',num:'610.00'},
					{lin:15,name:'深夜秀',num:'513.67'},
					{lin:16,name:'马大姐',num:'361.00'},
					]
				}
			}
		},
		methods:{
			home_home:function(){
				alert('home')
			},
			href_href:function(){
				alert(3)
			},
			my_home:function(){
				console.log(3)
			},
			aa:function(){
				this.$router.push({
						path: '/login',
					})
				}
		},
		beforeCreate:function(){
			
		},
		created:function(){
			document.title='主页'
		}
	}
</script>

<style lang="scss">
	
	.el-divider--horizontal{
		margin: 0;
	}
	.home .sp{
		color:#1890ff;
	}
	body{
		font-family: Chinese Quote,-apple-system,BlinkMacSystemFont,Segoe UI,PingFang SC,Hiragino Sans GB,Microsoft YaHei,Helvetica Neue,Helvetica,Arial,sans-serif,Apple Color Emoji,Segoe UI Emoji,Segoe UI Symbol;
		font-size: 15px;
		color:#303133;
		a{
			color:#1890ff;
			text-decoration: none;
			font-size: 14px;
		}
	}
	.body-contain{
		position: relative;
		width:  1000px;
		margin: 70px auto 0 auto;
		.main{
			position: relative;
			margin-right: 350px;
			.comments{
				 box-shadow: 0 2px 12px 0 rgba(0, 0, 0, 0.1);
				 .title{
					height: 70px;
					line-height: 70px;
					border-bottom: 1px solid #dcdfe6;
					padding-left: 20px;
				}
				.items{
					padding: 30px;
					.lists{
						
					}
				}
				
			}
			.showlists{
				.item{
					height: 220px;padding: 20px 20px 0 20px;
					.dd{
						width: 19%;padding-top:7px;margin-right:6px;box-sizing:border-box;float:left;height:100%;
						.photo{
							height: 150px;width: 100%;
						}
					}
				}
				 box-shadow: 0 2px 12px 0 rgba(0, 0, 0, 0.1);
				 margin-bottom:40px;
				.title{
					height: 70px;
					line-height: 70px;
					border-bottom: 1px solid #dcdfe6;
					padding-left: 20px;
				}
				.items{
					.el-carousel__indicator.el-carousel__indicator--horizontal.is-active button{
						opacity: 1;
					}
					.el-carousel__button{
						background-color: #c0c4cc;
						opacity: 0.24;
					}
				}
			}
			
		}
		.aside{
			font-size: 14px;
			width: 300px;
			position: absolute;
			right: 0;
			top:0;
			.contents{
				margin-bottom: 20px;
				.lists{
					padding: 10px 4px;
					li{
						padding: 3px;
						border-bottom: 1px dashed #dcdfe6;
						.num{
							float:right
						}
					}
				}
			}
			.el-card__body{
				padding: 0 20px;
			}
			.title{
				height: 70px;
				line-height: 70px;
				span{
					position: relative;
					right: -80px;
					bottom: -20px;
				};
			}
			.text{
				height: 70px;
				line-height: 70px;
			}
		}
	}
	.head-contain{
		background: white;
		.el-card__body{
			padding: 0;
		}
		width: 100%;
		height: 52px;
		position: fixed;
		z-index: 4;
		.header{
			position: relative;
			width: 1000px;
			margin:0 auto;
			.header_info{
				margin: 0 20px;
				line-height: 52px;
			}
			.search{
				input{
					padding-left: 20px;
					border: 1px solid #dcdfe6;
					height: 28px;
					width: 300px;
					border-radius:  5px 0 0 5px;
				}
				button{
					border-radius:  0 5px 5px 0;
				}
				display: inline-block;
				position: absolute;
				left: 30%;
				height: 52px;
				line-height: 52px;
			}
			.person_info{
				position: absolute;
				right: 0;
				top: 52px;
				height: 100px;
				width: 100px;
				background: white;
				.keke{
					height: 30px;
					line-height: 30px;
					padding: 10px 20px;
					cursor: pointer;
				}
			}
			.people{
				float: right;
				height: 52px;
				width: 52px;
				border-radius:50%;
				overflow: hidden;
				img{
					height: 100%;
					width: 100%;
				}
			}
		}
	}
</style>


